import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {WorkflowManagmentModule} from "./workflow-managment/workflow-managment.module";

const routes: Routes = [
  {path: '/', redirectTo: '/wm', pathMatch: 'full'},
  {path: 'wm', loadChildren: 'workflow-management/workflow-management.module#WorkflowManagmentModule'}

];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
